import axios from 'axios'

const request = axios.create({
    baseURL: '/api',  // Ê≥®ÊÑèÔºÅÔºÅ ËøôÈáåÊòØÂÖ®Â±ÄÁªü‰∏ÄÂä†‰∏ä‰∫Ü '/api' ÂâçÁºÄÔºå‰πüÂ∞±ÊòØËØ¥ÊâÄÊúâÊé•Âè£ÈÉΩ‰ºöÂä†‰∏ä'/api'ÂâçÁºÄÂú®ÔºåÈ°µÈù¢ÈáåÈù¢ÂÜôÊé•Âè£ÁöÑÊó∂ÂÄôÂ∞±‰∏çË¶ÅÂä† '/api'‰∫ÜÔºåÂê¶Âàô‰ºöÂá∫Áé∞2‰∏™'/api'ÔºåÁ±ª‰ºº '/api/api/user'ËøôÊ†∑ÁöÑÊä•ÈîôÔºåÂàáËÆ∞ÔºÅÔºÅÔºÅ
    // timeout: 1000 * 60,
})

// request Êã¶Êà™Âô®
// ÂèØ‰ª•Ëá™ËØ∑Ê±ÇÂèëÈÄÅÂâçÂØπËØ∑Ê±ÇÂÅö‰∏Ä‰∫õÂ§ÑÁêÜ

request.interceptors.request.use(config => {
    config.headers['Content-Type'] = 'application/json;charset=utf-8';
    return config
}, error => {
    return Promise.reject(error)
});

// response Êã¶Êà™Âô®
// ÂèØ‰ª•Âú®Êé•Âè£ÂìçÂ∫îÂêéÁªü‰∏ÄÂ§ÑÁêÜÁªìÊûú
request.interceptors.response.use(
    response => {
        let res = response.data;
        // Â¶ÇÊûúÊòØËøîÂõûÁöÑÊñá‰ª∂
        if (response.config.responseType === 'blob') {
            return res
        }
        // ÂÖºÂÆπÊúçÂä°Á´ØËøîÂõûÁöÑÂ≠óÁ¨¶‰∏≤Êï∞ÊçÆ
        if (typeof res === 'string') {
            res = res ? JSON.parse(res) : res
        }
        return res;
    },
    error => {
        console.log('err' + error) // for debug
        return Promise.reject(error)
    }
)


export default request

